export default {
  name: "_id",
  data(){
    return{
      post:'',
    }
  },
  created() {
    this.getSinglePost();
  },
  methods: {
    async getSinglePost() {
      const data = await this.$axios.$get('https://jsonplaceholder.typicode.com/posts/'+this.$route.params.id)
      this.post = data;
    }
  }
}
